import { useRef, useState } from 'react'
import { useSpring, animated, interpolate } from 'react-spring'
import theme from '../../theme'

const StandardInput = ({ placeholder = 'Name' }) => {
  const [isSpring, setIsSpring] = useState(false)
  const { o, color, opacity } = useSpring({
    from: { o: 0.8, color: theme.palette.shade[30], opacity: 0 },
    o: isSpring ? 1 : 0.8,
    color: isSpring ? theme.palette.blue : theme.palette.shade[30],
    opacity: isSpring ? 30 : 0
  })
  const inputStyle = {
    border: interpolate([o, color], (o, c) => `${o * 1}px solid ${c}`),
    outline: 'none',
    borderRadius: `${theme.spacing / 2}px`,
    padding: `${theme.spacing}px ${theme.spacing * 1.5}px`
  }
  const titleStyle = {
    opacity: opacity,
    display: 'block',
    position: 'absolute',
    top: 0,
    fontSize: `${theme.font.size.small}px`,
    backgroundColor: theme.palette.blue,
    color: `${theme.palette.shade[0]}`,
    marginLeft: `${theme.spacing}px`,
    padding: `${theme.spacing * 0.2}px ${theme.spacing}px`,
    borderTopRightRadius: `${theme.spacing / 2}px`,
    borderTopLeftRadius: `${theme.spacing / 2}px`
  }
  return (
    <>
      <div id="standard-input">
        <animated.span style={titleStyle}>{placeholder}</animated.span>
        <animated.input
          type="text"
          style={inputStyle}
          onFocus={() => setIsSpring(true)}
          onBlur={() => setIsSpring(false)}
        />
      </div>
      <style jsx>{`
        #standard-input {
          position: relative;
        }
      `}</style>
    </>
  )
}

export default StandardInput
